import React, { Component } from 'react';
import { connect } from 'react-redux';
import Button from 'element-react/dist/npm/es5/src/button';
import SummaryTitle from '../Parts/SummaryTitle/SummaryTitle';
import i18n from '../../language';
import './dapp.scss';

class Dapp extends Component {
  render() {
    return (
      <div className="summary-wrapper-light">
        <div id="anchor-dapp" className="anchor" />
        <div className="content-center-wrapper">
          <SummaryTitle
            index="03"
            title={i18n.t('dapp')}
          />
          <p className="dapp-text-content">{i18n.t('vision_content', {fullName: this.props.fullName})}</p>
          <div className="column-center">
            <p className="dapp-text-coming">{ i18n.t('PRDT_is_coming', {tokenName: this.props.tokenName}) }</p>
            <div className="dapp-img-wrapper">
              <img className="dapp-img-logo" src={require('../../images/60s.png')} alt="PRDT" />
            </div>
            <p className="text-medium-dark">Endless 60s</p>
            <p className="text-medium-grey align-center">{ i18n.t('PRDT_describe') }</p>
            <Button
              onClick={() => {
                window.location.href = this.props.endlessUrl;
              }}
              type="primary"
            >
              { i18n.t('PRDT_btn') }
            </Button>
          </div>
        </div>
      </div>
    )
  }
}

function mapStateToProps(state) {
  return {
    language: state.language,
    endlessUrl: state.endlessContract.endlessUrl,
    tokenName: state.icoInfo.name,
    fullName: state.icoInfo.fullName,
  };
}

export default connect(mapStateToProps)(Dapp);
