import ScatterJS from "scatterjs-core";
import ScatterEOS from "scatterjs-plugin-eosjs";
import Message from "../components/Parts/Message/Message";
import {initEOSClient, getEOSClient, clearEOSClient, getNetwork, getReadOnlyEOS} from "../networks";
import { /*getRemoteTime, startCount, */getTableRows } from "./time";
import i18n from "../language";

export const UPDATE_IDENTITY = Symbol('UPDATE_IDENTITY');
export const UPDATE_PRDT_INFO = Symbol('UPDATE_PRDT_INFO');
export const CHANGE_LANGUAGE = Symbol('CHANGE_LANGUAGE');
export const UPDATE_ENDLESS_URL = Symbol('UPDATE_ENDLESS_URL');


ScatterJS.plugins(new ScatterEOS());

let isConnected;

// 自动登陆
export function initApp() {
  return async (dispatch, getState) => {
    const { language } = getState();

    // 注册自动登录
    document.addEventListener('scatterLoaded', () => {
      connect()(dispatch, getState);
    });

    // 设置本地语言
    i18n.changeLanguage(language, () => {
      // changeLanguage(language)(dispatch, getState);
    });

    // 同步remote时间
    // getRemoteTime()(dispatch, getState);

    // 获取游戏round信息
    getTableRows()(dispatch, getState);

    // 计算剩余时间
    // startCount()(dispatch, getState);

    // 获取endless url
    getEndlessUrl()(dispatch, getState)
  }
}

// 更新语言
export function changeLanguage(language) {
  return async (dispatch) => {
    i18n.changeLanguage(language);
    return dispatch({ type: CHANGE_LANGUAGE, payload: { language } })
  }
}

// 手动登陆
export function login() {
  return function (dispatch) {
    console.log(isConnected, 'EOSClient');
    if (!isConnected) {
      connect()(dispatch);
    } else {
      initEOSClient();
      getId()(dispatch);
    }
  }
}

// 登出
export function logout() {
  return function (dispatch) {
    ScatterJS.logout()
      .then((res) => {
        console.log(res, '登出成功');
        dispatch({ type: UPDATE_IDENTITY, payload: { identity: null } });
        clearEOSClient();
        console.log('登出成功')
      })
      .catch(() => {
        Message('logout_fail', { type: 'error' });
        console.log('登出失败')
      })
  }
}

// 获取身份
function getId() {
  return (dispatch) => {
    ScatterJS.scatter && ScatterJS.scatter.getIdentity({ accounts: [getNetwork()] })
      .then(() => {
        // 获取身份成功
        const identity = ScatterJS.account('eos');
        console.log(identity, 'this.scatter.identity.accounts');
        dispatch({ type: UPDATE_IDENTITY, payload: { identity } });
        Message('login_success', { type: 'success' });
      })
      .catch(() => {
        Message('login_fail', { type: 'error' });
      });
  }
}

// 合约操作
export function transfer({
  contract = 'eosio.token',
  actionName = 'transfer',
  to,
  quantity = '',
  memo = ''
}) {
  return (dispatch, getState) => {
    const { identity = {} } = getState();
    if (!identity) {
      return Promise.reject('未登陆账户');
    }
    return getEOSClient().transaction(
      {
        // ...headers,
        actions: [
          {
            account: contract,
            name: actionName,
            authorization: [{
              actor: identity.name,
              permission: identity.authority
            }],
            data: {
              from: identity.name,
              to: to,
              quantity,
              memo
            }
          }
        ]
      }
    )
      .catch(err => {
        return Promise.reject(err);
      });
  }
}

// 获取Endless地址
function getEndlessUrl() {
  return (dispatch, getState) => {
    const { contract, table } = getState().endlessContract;
    return getReadOnlyEOS()
      .getTableRows({
        code: contract,
        json: true,
        scope: contract,
        table: table,
      })
      .then(res => {
        console.log('获取Endless url：', res);
        const { rows = [] } = res || {};
        const { data: endlessUrl } = rows[3];
        if (endlessUrl) {
          dispatch({
            type: UPDATE_ENDLESS_URL,
            payload: { endlessUrl }
          });
        }
      })
  }

}

// connect to scatter
function connect () {
  return (dispatch, getState) => {
    ScatterJS.scatter.connect("p3d")
      .then(connected => {
        // User does not have Scatter Desktop, Mobile or Classic installed.
        isConnected = connected;
        if (!connected) {
          Message('login_fail', { type: 'error' });
          return false;
        }

        // connect 之后 EOSClient有发送等功能
        initEOSClient();

        /* --- test --- */
        // (ScatterJS.scatter.eos === window.scatter.eos) => true 引用同一个对象

        // getEOSClient.getCurrencyBalance('prdttesticco', "jungletestco").then(r => console.log(r, 'balance'));

        // getEOSClient.contract('eosio.token').then(r => console.log(r, 1919919199));
        // getEOSClient.contract('ethsidechain').then(r => console.log(r, 'ethsidechain'));
        // getEOSClient.getAbi('hazdembvhage').then(r => console.log(r, 'getAbi'));
        // console.log(ScatterJS.scatter.eos === window.scatter.eos, '触发scatterLoaded');
        /* --- test --- */

        getId()(dispatch, getState);

        window.scatter = null;
        window.ScatterJS = null;
      })
      .catch(err => {
        alert(err);
        console.log(err, 'connect')
      });
  }
}
