import React, {Component} from 'react';
import {connect} from 'react-redux';
import Message from '../Parts/Message/Message';
import i18n from '../../language';


class Share extends Component {
  logo;
  walletLogo;
  qrCode;
  canvas;
  ctx;

  componentDidMount() {
    // this.logo = new Image();
    // this.logo.src = require('../../images/logo.png');
    //
    // this.qrCode = new Image();
    // this.qrCode.src = require('../../images/pie.png');
    //
    // this.walletLogo = new Image();
    // this.walletLogo.src = require('../../images/wallet_logo.png');
    //
    // this.canvas = document.createElement('canvas');
    //
    // if (this.canvas.getContext){
    //   this.ctx = this.canvas.getContext('2d');
    // }
    //
    // setTimeout(() => {
    //   this.getImage();
    // }, 3000)
  }

  // 画出分享图
  getImage = () => {
    const font = (size) => `${size}px Helvetica, Tahoma, Arial, STXihei, “华文细黑”, Heiti, “黑体”, “Microsoft YaHei”, “微软雅黑”, SimSun, “宋体”, sans-serif`;
    const { progress, /*timeLeft,*/ tokenName, exchangeRate, fullName } = this.props;
    if (this.ctx){
      this.canvas.height = 1638;
      this.canvas.width = 720;

      // 背景色
      this.ctx.fillStyle = "#120F1B";
      this.ctx.fillRect(0, 0, this.canvas.width, this.canvas.height);
      // this.ctx.drawImage(this.image, 0, 0);

      // logo
      this.ctx.drawImage(this.logo, 60, 60, 40, 40);

      // 卡片框
      this.ctx.fillStyle = "#211D2D";
      this.ctx.lineJoin = "round";
      this.ctx.lineWidth = 6;
      this.ctx.fillRect(28, 556, this.canvas.width - ( 28 * 2 ), 292);

      // 白色文字
      this.ctx.font = font(62);
      this.ctx.fillStyle = "#fff";
      this.ctx.fillText(i18n.t('share_slogan_1'), 70, 250);
      this.ctx.fillText(i18n.t('share_slogan_2'), 70, 340);

      // Tricolor tower
      this.ctx.font = font(28);
      this.ctx.fillText(i18n.t('app_name'), 120, 90);

      // DappFinder
      this.ctx.fillText(i18n.t('share_dapp_platform'), 325, this.canvas.height - 186);
      this.ctx.drawImage(this.walletLogo, 230, this.canvas.height - 210, 70, 70);

      // 财富之旅
      this.ctx.fillText(i18n.t('share_fortune_journeys'), 232, 996);


      // 预售中 | 已经结束
      this.ctx.font = font(32);
      this.isEnded = parseInt(progress) === 1 || this.props.end/* || timeLeft <= 0*/;
      const cardTitle = this.isEnded ? i18n.t('sell_card_title_ended', {name: tokenName}) : i18n.t('sell_card_title', {name: tokenName});
      this.ctx.fillText(cardTitle, 60, 635);

      // 识别二维码
      this.ctx.fillText(i18n.t('share_extract_qr_code'), 200, 920);

      // 兑换
      this.ctx.font = font(24);
      this.ctx.fillText(`1 EOS = ${exchangeRate} ${tokenName}`, 270, 800);


      // 灰色文字
      this.ctx.fillStyle = 'rgba(255,255,255,0.65)';
      this.ctx.font = font(24);
      this.ctx.fillText(i18n.t('share_subhead_1', {fullName}), 60, 420);
      this.ctx.fillText(i18n.t('share_subhead_2'), 60, 455);
      this.ctx.fillText(i18n.t('share_subhead_3'), 60, 490);

      this.ctx.font = font(20);
      this.ctx.fillText(i18n.t('share_dapp_ecology'), 325, this.canvas.height - 140);

      // 已售
      this.ctx.font = font(28);
      this.ctx.fillText(i18n.t('share_sum_sold'), 600, 650);


      // 橘色文字
      this.ctx.font = font(68);
      this.ctx.fillStyle = '#F06051';
      const progressFormatted = parseFloat((progress * 100).toFixed(2)) +  '%';
      this.ctx.fillText(progressFormatted, 580 - this.ctx.measureText(progressFormatted).width, 652);

      // 进度条
      const lineWidth = (this.canvas.width - 120) * progress + 60;

      this.ctx.lineWidth = 16;
      this.ctx.strokeStyle = '#fff';
      this.ctx.beginPath();
      this.ctx.moveTo(60, 740);
      this.ctx.lineTo(this.canvas.width - 60, 740);
      this.ctx.lineCap = "round";
      this.ctx.stroke();

      this.ctx.strokeStyle = '#F06051';
      this.ctx.beginPath();
      this.ctx.moveTo(60, 740);
      this.ctx.lineTo(lineWidth, 740);
      this.ctx.stroke();

      // this.ctx.strokeStyle = '#F06051';
      // this.ctx.beginPath();
      // this.ctx.moveTo(60 + 5, 740);
      // this.ctx.lineTo(lineWidth + 8, 740);
      // this.ctx.lineCap = "butt";
      // this.ctx.stroke();

      // 倒三角
      const baseX = 322;
      const baseY = 1040;
      const triW = 36;
      const triH = 28;
      this.ctx.lineWidth = 2;
      this.ctx.beginPath();
      this.ctx.moveTo(baseX, baseY);
      this.ctx.lineTo(baseX + triW, baseY);
      this.ctx.arcTo(baseX + triW + 8, baseY, baseX + (triW / 2), baseY + triW, 2);
      this.ctx.lineTo(baseX + (triW / 2) + 4, baseY + triH);
      this.ctx.arcTo(baseX + (triW / 2), baseY + triH + 4, baseX, baseY, 2);
      this.ctx.lineTo(baseX, baseY + 8);
      this.ctx.arcTo(baseX - 6, baseY, baseX, baseY, 2);
      this.ctx.lineTo(baseX + triW, baseY);
      this.ctx.stroke();

      // document.getElementById('root').insertBefore(this.canvas, document.querySelector('.header-modal-inactive'));

      return this.canvas.toDataURL();
    }

    Message(i18n.t('share_not_support'), { type: 'error' });
  };

  onClick = () => {
    // 分享图
    // this.props.handleClick && this.props.handleClick();
    // const data = this.getImage();
    //
    // window.ReactNativeWebView &&
    // data &&
    // window.ReactNativeWebView.postMessage(JSON.stringify({channel: 'native-share', data}))

    // 分享文本
    const data = i18n.t('share_link');

    window.ReactNativeWebView &&
    data &&
    window.ReactNativeWebView.postMessage(JSON.stringify({channel: 'native-share', data}))
  };

  render () {
    if (!window.ReactNativeWebView) return null;
    return (
      <a onClick={this.onClick}>
        <p className={this.props.className}>{i18n.t('btn_share')}</p>
      </a>
    )
  }
}

function mapStateToProps(state) {
  return {
    language: state.language,
    progress: state.icoInfo.progress,
    tokenName: state.icoInfo.name,
    fullName: state.icoInfo.fullName,
    exchangeRate: parseFloat(state.icoInfo.exchangeRate.toFixed(2)),
    // timeLeft: state.timeLeft,
    end: state.icoInfo.end
  };
}


export default connect(mapStateToProps)(Share);

