import {
  UPDATE_IDENTITY,
  CHANGE_LANGUAGE,
  UPDATE_PRDT_INFO,
  UPDATE_ENDLESS_URL,
} from '../actions';
import {UPDATE_REMOTE_TIME, UPDATE_TIME_LEFT} from '../actions/time';
import i18n from '../language';

const initialState = {
  i18n,
  language: void (0),
  socialContact: 't.me/aaa',
  customerService: 't.me/aaa',
  mail: '99thetheme@gmail.com',
  endlessContract: {
    contract: 'helix1234513',
    table: 'finderarg',
    endlessUrl: void (0)
  },
  icoInfo: {
    name: 'HLK',
    fullName: 'HELIX',
    contract: 'helix1234514',
    table: 'stat',
    type: 'EOS Token',
    maxSupply: 0, // token总量
    symbol: 'HLK',
    startTime: 0,
    endTime: 0,
    exchangeRate: 100,
    minPayments: 1,
    progress: 0,
    occupy: 0.4, // 销售占比
    account: 'helix1234514',
    end: false
  },
  identity: null,
  timeOffset: 0, // 服务器时间校正
  timeLeft: 0, // 当局剩余时间
  // timeTotal: 30 * 24 * 3600 * 1000, // ico周期
  FAQ: [
    {
      question: () => i18n.t('qu1', {name: 'HLK'}),
      answer: () => i18n.t('ans1', {name: 'HLK', fullName: 'HELIX'})
    },
    {
      question: () => i18n.t('qu2'),
      answer: () => i18n.t('ans2', {name: 'HLK', fullName: 'HELIX'})
    },
    {
      question: () => i18n.t('qu3'),
      answer: () => i18n.t('ans3')
    }
  ]
};

export default function (state = initialState, action) {
  switch (action.type) {
    case CHANGE_LANGUAGE:
      return {...state, language: action.payload.language};
    case UPDATE_IDENTITY:
      return {...state, identity: action.payload.identity};
    case UPDATE_REMOTE_TIME:
      return {...state, timeOffset: action.payload.timeOffset};
    case UPDATE_TIME_LEFT:
      return {...state, timeLeft: action.payload.timeLeft};
    case UPDATE_PRDT_INFO:
      return {...state, icoInfo: {...state.icoInfo, ...action.payload}};
    case UPDATE_ENDLESS_URL:
      return {...state, endlessContract: {...state.endlessContract, ...action.payload}};
    default:
      return state;
  }
};
